<template>
  <section>
    <div v-if="loading">
      <h3>Loading...</h3>
    </div>

    <div v-if="error">
      <h1>{{error}}</h1>
      <router-link to="/">Go Back</router-link>
    </div>

    <div v-if="profileData" class="container">

      <router-link class="home-button" to="/">Go Back</router-link>

      <div class="name">
        <img :src="profileData.platformInfo.avatarUrl" alt class="platform-avatar" />
        <h4>
          {{profileData.platformInfo.platformUserHandle}} ({{profileData.platformInfo.platformUserId}})
        </h4>
      </div>

      <div class="grid">
        <div>
          <ul>
            <li>
              <h4>Total Time Played</h4>
              <p>{{profileData.segments[0].stats.timePlayed.displayValue}}</p>
            </li>
            <li>
              <h4>Total Damage Dealt</h4>
              <p>{{profileData.segments[0].stats.damage.displayValue}}</p>
            </li>
            <li>
              <h4>Wins</h4>
              <p>{{profileData.segments[0].stats.wins.displayValue}}</p>
            </li>
            <li>
              <h4>Losses</h4>
              <p>{{profileData.segments[0].stats.losses.displayValue}}</p>
            </li>
            <li>
              <h4>MVPs</h4>
              <p>{{profileData.segments[0].stats.mvp.displayValue}}</p>
            </li>
            <li>
              <h4>Money Earned</h4>
              <p>${{profileData.segments[0].stats.moneyEarned.displayValue}}</p>
            </li>
            <li>
              <h4>K/D</h4>
              <p>
                {{profileData.segments[0].stats.kd.displayValue}}
                <span>{{profileData.segments[0].stats.kd.percentile}}%</span>
              </p>
            </li>
            <li>
              <h4>Matches Played</h4>
              <p>{{profileData.segments[0].stats.matchesPlayed.displayValue}}</p>
            </li>
            <li>
              <h4>Kills</h4>
              <p>{{profileData.segments[0].stats.kills.displayValue}}</p>
            </li>
            <li>
              <h4>Deaths</h4>
              <p>{{profileData.segments[0].stats.deaths.displayValue}}</p>
            </li>
            <li>
              <h4>Snipers Killed</h4>
              <p>{{profileData.segments[0].stats.snipersKilled.displayValue}}</p>
            </li>
            <li>
              <h4>Bombs Planted</h4>
              <p>{{profileData.segments[0].stats.bombsPlanted.displayValue}}</p>
            </li>
            <li>
              <h4>Bombs Defused</h4>
              <p>{{profileData.segments[0].stats.bombsDefused.displayValue}}</p>
            </li>
            <li>
              <h4>Headshots</h4>
              <p>
                {{profileData.segments[0].stats.headshots.displayValue}}
                <span>{{profileData.segments[0].stats.headshotPct.displayValue}}</span>
              </p>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import axios from 'axios';
  export default {
    name: "Profile",
    data() {
      return {
        loading: false,
        error: null,
        profileData: null
      };
    },
    beforeCreate() {
      document.body.className = "body-bg-no-image";
    },
    async created() {
      this.loading = true;
      try {
        const res = await axios.get(`/api/v2/profile/steam/${this.$route.params.name}`);
        this.profileData = res.data.data;
        console.log(this.profileData);
        this.loading = false;
      } catch (error) {
        this.loading = false;
        this.error = error.response.data.message;
      }
    }
  }
</script>

<style scoped>
  .container {
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    max-width: 700px;
    margin: 1rem auto;
    padding: 2rem 1.5rem;
    border-radius: 20px;
  }

  .home-button {
    margin-left: 0.5rem;
    margin-bottom: 2rem;
  }

  div.name {
    font-size: 2rem;
    background: rgba(0, 0, 0, 0.6);
    padding: 0.3rem 0.5rem;
    text-align: center;
    border-radius: 20px;
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
  }

  a {
    display: inline-block;
    margin-top: 2rem;
    border: #fff 2px solid;
    padding: 0.5rem 0.8rem;
  }

  a:hover {
    border: #ccc 2px solid;
    color: #ccc;
  }

  .platform-avatar {
    width: 200px;
    height: 200px;
    margin-right: 0.7rem;
  }

  img {
    width: 100%;
  }

  .grid {
    display: grid;
    text-align: center;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 1rem;
  }

  li {
    background: rgba(0, 0, 0, 0.6);
    padding: 1rem;
    margin-bottom: 0.7rem;
    border-radius: 10px;
  }

  li p {
    font-size: 2rem;
  }

  li:first-child p {
    font-size: 1.5rem;
  }

  li span {
    font-size: 1rem;
    color: #ccc;
  }

  @media (max-width: 500px) {
    h1 {
      font-size: 1.5rem;
      display: block;
      text-align: center;
    }

    .platform-avatar {
      display: none;
    }

    .grid {
      grid-template-columns: 1fr;
    }

    li p {
      font-size: 1.5rem;
    }
  }

</style>
