<template>
  <section class="search">
    <h1>Track Player Stats</h1>
    <form v-on:submit.prevent="onSubmit">
      <div class="form-group">
        <label for="name">Name</label>
        <input type="text" name="text" v-model="name" id="name" placeholder="Steam ID, Steam Community URL, etc." />
      </div>
      <div class="form-group">
        <input type="submit" value="Submit" class="btn" />
      </div>
    </form>
  </section>
</template>

<script>
  export default {
    name: "Search",
    data() {
      return {
        name: '',
      };
    },
    beforeCreate() {
      document.body.className = "body-bg-image";
    },
    methods: {
      onSubmit() {
        if (!this.name) {
          this.$toasted.show("Please enter a STEAMID, or Steam name", {
            duration: 3000,
            icon: "exclamation-circle"
          });
        } else {
          this.$router.push(`/profile/steam/${this.name}`);
        }
      }
    }
  }
</script>

